const totalColors = 4; // values: 1-4
const totalFacets = 3; // values: 0-2
const totalPlaces = 9; // values: 0-8
const totalCells = 27; // values: 0-26


function getCellId(itemTokenId) {
  return itemTokenId % totalCells;
}

function getPlaceId(itemTokenId) {
  return itemTokenId % totalPlaces;
}

function getFacetId(itemTokenId) {
  if (itemTokenId % totalCells < totalPlaces) return 0;
  else if (itemTokenId % totalCells < totalPlaces * 2) return 1;
  else return 2;
}

function getColorId(itemTokenId) {
  return itemTokenId % totalColors + 1;
}

function getColor(itemTokenId) {
  switch (getColorId(itemTokenId)) {
    case 1: return 'red';
    case 2: return 'green';
    case 3: return 'blue';
    case 4: return 'purple';
    case 0:
    default: return 'grey';
  }
}
function getColorCell(value) {
  switch (value) {
    case 1: return 'red';
    case 2: return 'green';
    case 3: return 'blue';
    case 4: return 'purple';
    case 0:
    default: return 'grey';
  }
}

function filterTokensForCell(list, cellId) {
  const newList = [{ key: 'Select', value: 'Select', text: 'Select' }];
  for (let i in list) {
    if (getCellId(list[i]) === cellId) {
      newList.push(
        { key: list[i], value: list[i], text: `${list[i]} (${getColor(list[i])})` },
      );
    }
  }
  return newList;
}

